<template>
  <v-card color="cardAnalisis">
    <v-row>
      <v-col v-if="!consultaSeleccionada" sm="12">
        <v-container>
          <v-row>
            <v-col sm="6">
              <v-menu
                ref="menu"
                v-model="menu"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="290px"
                
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    
                    v-model="formatDesde"
                    label="Desde"
                    hint="DD/MM/YYYY"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  ref="picker"
                  v-model="desde"
                  
                  :first-day-of-week="1"
                  locale="es-es"
                  :max="new Date().toISOString().substr(0, 10)"
                  min="1950-01-01"
                ></v-date-picker>
              </v-menu>
            </v-col>
            <v-col sm="6">
              <v-menu
                ref="menu"
                v-model="menu2"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="formatHasta"
                    label="Hasta"
                    hint="DD/MM/YYYY"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  ref="picker"
                  v-model="hasta"
                  :first-day-of-week="1"
                  locale="es-es"
                  :max="new Date().toISOString().substr(0, 10)"
                  min="1950-01-01"
                ></v-date-picker>
              </v-menu>
            </v-col>
          </v-row>
        </v-container>
        <v-container style="max-height: 70%" class="overflow-y-auto">
          <v-row>
            <v-col v-for="(consulta, i) in consultas" :key="i" sm="3">
              <CardHistorialCita
                :especialista="consulta.especialista"
                :fecha="consulta.fecha"
                :hora="consulta.hora"
                :id="i"
                @clicked="onConsultaSeleccionada"
              />
            </v-col>
          </v-row>
        </v-container>
      </v-col>
      <v-col v-if="consultaSeleccionada" sm="6">
        <v-container>
          <v-row>
            <v-col sm="6">
              <v-menu
                ref="menu"
                v-model="menu"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="formatDesde"
                    label="Desde"
                    hint="DD/MM/YYYY"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  ref="picker"
                  v-model="desde"
                  :first-day-of-week="1"
                  locale="es-es"
                  :max="new Date().toISOString().substr(0, 10)"
                  min="1950-01-01"
                ></v-date-picker>
              </v-menu>
            </v-col>
            <v-col sm="6">
              <v-menu
                ref="menu"
                v-model="menu2"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="formatHasta"
                    label="Hasta"
                    hint="DD/MM/YYYY"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  ref="picker"
                  v-model="hasta"
                  :first-day-of-week="1"
                  locale="es-es"
                  :max="new Date().toISOString().substr(0, 10)"
                  min="1950-01-01"
                ></v-date-picker>
              </v-menu>
            </v-col>
          </v-row>
        </v-container>
        <v-container style="max-height: 70%" class="overflow-y-auto">
          <v-row>
            <v-col v-for="(consulta, i) in consultas" :key="i" sm="6">
              <CardHistorialCita
                :especialista="consulta.especialista"
                :fecha="consulta.fecha"
                :hora="consulta.hora"
                :id="i"
                @clicked="onConsultaSeleccionada"
              />
            </v-col>
          </v-row>
        </v-container>
      </v-col>
      <v-col v-if="consultaSeleccionada" sm="6">
        <v-row>
          <v-col sm="12">
            <h2>Consulta seleccionada</h2>
          </v-col>
          <v-col sm="6"> <b>Fecha: </b>{{ consultaSeleccionada.fecha }} </v-col>
          <v-col sm="6"> <b>Hora: </b>{{ consultaSeleccionada.hora }} </v-col>
          <v-col sm="12">
            <b>Mdico: </b>{{ consultaSeleccionada.medico }}
          </v-col>
          <v-col sm="12">
            {{ consultaSeleccionada.informe }}
          </v-col>
          <v-col sm="12">
            <v-layout justify-center>
              <v-btn color="accept">Descargar justificante</v-btn>
            </v-layout>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import CardHistorialCita from "@/components/CardHistorialCita.vue";
let consultas = [
  {
    especialista: "Cabecera",
    fecha: "12/12/2020",
    hora: "15:30",
    medico: "Dr. Borja Ponga Carro",
    informe: "El paciente tiene el dedo gordo del pie roto",
  },
  {
    especialista: "Fisioterapeuta",
    fecha: "13/12/2020",
    hora: "18:00",
    medico: "Dr. lvaro Can Can",
    informe: "El paciente tiene el dedo gordo de la mano roto",
  },
  {
    especialista: "Dentista",
    fecha: "14/12/2020",
    hora: "10:30",
    medico: "Dr. Miguel Fernndez Herrero",
    informe: "El paciente tiene un esguince en el tobillo",
  },
  {
    especialista: "Cabecera",
    fecha: "15/12/2020",
    hora: "15:30",
    medico: "Dr. Borja Ponga Carro",
    informe: "El paciente tiene el dedo gordo del pie roto",
  },
  {
    especialista: "Fisioterapeuta",
    fecha: "16/12/2020",
    hora: "18:00",
    medico: "Dr. lvaro Can Can",
    informe: "El paciente tiene el dedo gordo de la mano roto",
  },
  {
    especialista: "Dentista",
    fecha: "17/12/2020",
    hora: "10:30",
    medico: "Dr. Miguel Fernndez Herrero",
    informe: "El paciente tiene un esguince en el tobillo",
  },
  {
    especialista: "Cabecera",
    fecha: "18/12/2020",
    hora: "15:30",
    medico: "Dr. Borja Ponga Carro",
    informe: "El paciente tiene el dedo gordo del pie roto",
  },
  {
    especialista: "Fisioterapeuta",
    fecha: "19/12/2020",
    hora: "18:00",
    medico: "Dr. lvaro Can Can",
    informe: "El paciente tiene el dedo gordo de la mano roto",
  },
  {
    especialista: "Dentista",
    fecha: "20/12/2020",
    hora: "10:30",
    medico: "Dr. Miguel Fernndez Herrero",
    informe: "El paciente tiene un esguince en el tobillo",
  },
];

export default {
  name: "Historial",
  components: {
    CardHistorialCita,
  },
  data() {
    return {
      consultas: consultas,
      consultaSeleccionada: null,
      desde: null,
      hasta: null,
      menu: false,
      menu2: false,
    };
  },
  computed: {
    formatDesde() {
      return this.formatDate(this.desde);
    },
    formatHasta() {
      return this.formatDate(this.hasta);
    },
  },
  methods: {
    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split("-");
      return `${day}/${month}/${year}`;
    },
    onConsultaSeleccionada(id) {
      this.consultaSeleccionada = this.consultas[id];
    },
  },
};
</script>

