<template>
  <v-card color="cardAnalisis">
    <v-row>
      <v-col sm="1"> </v-col>
      <v-col sm="3">
        <v-container>
          <v-row>
            <v-col ms="12">
              <v-layout justify-center>
                <h3>Citas activas</h3>
              </v-layout>
            </v-col>
            <v-col v-for="(consulta, i) in consultas" :key="i" cols="12">
              <CardProximaCita
                :especialista="consulta.especialista"
                :fecha="consulta.fecha"
                :hora="consulta.hora"
                :id="i"
                :urgencia="consulta.urgencia"
                @clicked="onEliminarCita"
              />
            </v-col>
          </v-row>
        </v-container>
      </v-col>
      <v-col sm="1"> </v-col>
      <v-col sm="6">
        <v-container>
          <v-row>
            <v-col sm="12">
              <v-menu
                ref="menu"
                v-model="menu"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="formatDia"
                    label="Selecciona un da"
                    hint="DD/MM/YYYY"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  ref="picker"
                  v-model="dia"
                  :first-day-of-week="1"
                  locale="es-es"
                  :disabled-days="disableWeekends"
                  :min="new Date().toISOString().substr(0, 10)"
                ></v-date-picker>
              </v-menu>
            </v-col>
            <v-col sm="12">
              <v-select
                v-model="hora"
                :items="horas"
                label="Selecciona una hora"
              ></v-select>
            </v-col>
            <v-col sm="12">
              <v-select
                v-model="especialista"
                :items="especialistas"
                label="Selecciona el especialista para su cita"
              ></v-select>
            </v-col>
            <v-col sm="3">
              <v-checkbox v-model="urgencia" :label="`Urgente`" color="selection"></v-checkbox>
            </v-col>
            <v-col sm="9">
              <v-text-field
                v-if="urgencia"
                v-model="razon"
                label="Razn de la urgencia"
                clearable
              ></v-text-field>
            </v-col>
            <v-col sm="12">
              <v-alert v-if="error" dense outlined type="error">
                <pre>{{ mensajeError }}</pre>
              </v-alert>
            </v-col>
            <v-col sm="12">
              <v-container>
                <v-layout justify-center>
                  <v-btn x-large color="accept" @click="onAnadirCita()"
                    >Pedir cita</v-btn
                  >
                </v-layout>
              </v-container>
            </v-col>
          </v-row>
        </v-container>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import CardProximaCita from "@/components/CardProximaCita.vue";
let consultas = [
  {
    especialista: "Fisioterapeuta",
    fecha: "29/12/2020",
    hora: "15:30",
    urgencia: false,
    razon: "",
  },
  {
    especialista: "Dentista",
    fecha: "30/12/2020",
    hora: "15:30",
    urgencia: true,
    razon: "Porque si",
  },
];

let horas = [
  "09:00",
  "09:30",
  "10:00",
  "10:30",
  "11:00",
  "11:30",
  "12:00",
  "12:30",
  "13:00",
  "13:30",
  "14:00",
  "14:30",
  "15:00",
  "15:30",
  "16:00",
  "16:30",
  "17:00",
  "17:30",
  "18:00",
  "18:30",
  "19:00",
  "19:30",
  "20:00",
  "20:30",
];
let especialistas = [
  "Cabecera",
  "Dentista",
  "Fisioterapeuta",
  "Psiclogo",
  "Traumatlogo",
];

export default {
  name: "PedirCita",
  components: {
    CardProximaCita,
  },
  data() {
    return {
      consultas: consultas,
      horas: horas,
      especialistas: especialistas,
      especialista: null,
      hora: null,
      urgencia: false,
      razon: "",
      dia: null,
      menu: false,
      error: false,
      mensajeError: "",
    };
  },
  computed: {
    formatDia() {
      return this.formatDate(this.dia);
    },
  },

  methods: {
    disableWeekends(date) {
      const day = new Date(date).getDay();
      return day === 0 || day === 6;
    },
    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split("-");
      return `${day}/${month}/${year}`;
    },
    onEliminarCita(id) {
      this.consultas.splice(id, 1);
    },
    onAnadirCita() {
      if (this.consultas.length == 3) {
        this.mensajeError = "Has alcanzado el nmero mximo de citas";
        this.error = true;
        return;
      } else {
        this.error = false;
      }
      let valido = true;
      let mensajeError = "";
      if (!this.formatDia) {
        valido = false;
        mensajeError += "Debes introducir una fecha\n";
      }
      if (!this.especialista) {
        valido = false;
        mensajeError += "Debes introducir un especialista\n";
      }
      if (!this.hora) {
        valido = false;
        mensajeError += "Debes introducir una hora\n";
      }
      if (this.urgencia && !this.razon) {
        valido = false;
        mensajeError += "Debes introducir la razn de la urgencia\n";
      }
      if (!valido) {
        this.mensajeError = mensajeError;
        this.error = true;
        console.log("cita no valida");
        return;
      }
      this.error = false;
      this.mensajeError = "";
      let nuevaCita = {
        especialista: this.especialista,
        fecha: this.formatDia,
        hora: this.hora,
        urgencia: this.urgencuia,
        razon: this.razon,
      };
      this.consultas.push(nuevaCita);
      this.ordenarConsultas();
    },
    ordenarConsultas() {
      this.consultas.sort(function(a, b) {
        if (a.fecha == b.fecha) {
          a = a.hora.split("/").join("");
          b = b.hora.split("/").join("");
          return a.localeCompare(b);
        }
        a = a.fecha
          .split("/")
          .reverse()
          .join("");
        b = b.fecha
          .split("/")
          .reverse()
          .join("");

        return a.localeCompare(b);
      });
    },
  },
};
</script>

