<template>
  <v-card @click="onSeleccionada()" color="componente">
    <v-card-text>
      <p><b>Especialista: </b>{{ especialista }}</p>
      <p><b>Fecha: </b>{{ fecha }}</p>
      <p><b>Hora: </b>{{ hora }}</p>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  name: "CardHistorialCita",
  props: {
    especialista: String,
    fecha: String,
    hora: String,
    id: Number,
  },
  methods: {
    onSeleccionada() {
      this.$emit('clicked', this.id)
    },
  },
};
</script>