<template>
  <v-card color="componente">
    <v-card-text>
        <b>Especialista: </b>{{especialista}}
        <br><b>Fecha: </b>{{fecha}}
        <br><b>Hora: </b>{{hora}} 
        <br><v-btn small color="cancel" @click.stop="dialog = true">Cancelar cita</v-btn>
        <v-dialog
      v-model="dialog"
      max-width="310"
    >
      <v-card>
        <v-card-title class="headline">
          Quieres eliminar la cita del da {{fecha}} a las {{hora}}?
        </v-card-title>

        <v-card-text>
          Si la eliminas debers pedir otra cita si quieres acudir a la consulta.
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="cancelgrey"
            text
            @click="dialog = false"
          >
            Cancelar
          </v-btn>

          <v-btn
            color="cancel"
            text
            @click="onEliminarCita()"
          >
            Eliminar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  name: "CardProximaCita",
  data() {
    return {
      dialog: false,
    };
  },
  props: {
    especialista: String,
    fecha: String,
    hora: String,
    urgencia: Boolean,
    id: Number,
  },
  methods: {
    onEliminarCita() {
      this.dialog = false;
      this.$emit("clicked", this.id);
    },
  },
};
</script>