<template >
  <v-card color="cardAnalisis">
    <v-container>
      <v-row>
        <v-col sm="3" md="3">
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            min-width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="formatDesde"
                label="Desde"
                hint="DD/MM/YYYY"
                prepend-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker
              ref="picker"
              v-model="desde"
              :max="new Date().toISOString().substr(0, 10)"
              min="1950-01-01"
            ></v-date-picker>
          </v-menu>
        </v-col>
        <v-col sm="3" md="3">
          <v-menu
            ref="menu"
            v-model="menu2"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            min-width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="formatHasta"
                label="Hasta"
                hint="DD/MM/YYYY"
                prepend-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker
              ref="picker"
              v-model="hasta"
              :max="new Date().toISOString().substr(0, 10)"
              min="1950-01-01"
            ></v-date-picker>
          </v-menu>
        </v-col>

        <table>
          <tr>
            <td class="tdpad">
              <v-checkbox  label="Radiografa"></v-checkbox>
            </td>
            <td class="tdpad">
              <v-checkbox label="Tomografa"></v-checkbox>
            </td>
            <td class="tdpad">
              <v-checkbox label="Resonancia"></v-checkbox>
            </td>
            <td class="tdpad">
              <v-checkbox label="Otros"></v-checkbox>
            </td>
          </tr>
        </table>
      </v-row>
      <v-divider></v-divider>
      <v-item-group>
        <v-container>
          <v-row>
            <v-col
              v-for="(consulta, i) in consultas"
              :key="i"
              sm="4"
              cols="12"
              md="4"
            >
              <v-item>
                <cardprueba
                  :nombre="consulta.nombre"
                  :fecha="consulta.fecha"
                  :imagen="consulta.imagen"
                  :tipo="consulta.tipo"
                  :informe="consulta.informe"
                />
              </v-item>
            </v-col>
          </v-row>
        </v-container>
      </v-item-group>
    </v-container>
  </v-card>
</template>

<script>
import CardPrueba from "../components/CardPrueba";
export default {
  components: {
    cardprueba: CardPrueba,
  },
  data() {
    return {
      consultas: consultas,
      consultaSeleccionada: undefined,
      desde: null,
      hasta: null,
      menu: false,
      menu2: false,
    };
  },

  computed: {
    formatDesde() {
      return this.formatDate(this.desde);
    },
    formatHasta() {
      return this.formatDate(this.hasta);
    },
  },
  methods: {
    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split("-");
      return `${day}/${month}/${year}`;
    },
    onConsultaSeleccionada(id) {
      this.consultaSeleccionada = this.consultas[id];
    },
  },
};

let consultas = [
  {
    nombre: "Radiografa",
    fecha: "20/6/2020",
    imagen: "fractura.jpg",
    tipo: "Radiografa",
    informe: "El paciente fmur roto y astillado",
  },
  {
    nombre: "Radiografa",
    fecha: "20/7/2020",
    imagen: "fractura.jpg",
    tipo: "Radiografa",
    informe: "El paciente fmur roto y astillado",
  },
  {
    nombre: "Radiografa Fmur",
    fecha: "20/9/2020",
    hora: "15:30",
    imagen: "fractura.jpg",
    tipo: "Radiografa",
    informe: "El paciente fmur roto y astillado",
  },
];
</script>

<style>
.tdpad {
  padding-left: 60px;
}
</style>