<template >

  <v-card
    class="mx-auto pa-6"
    max-width="344"
    color="componente"
  >
    <v-img
      v-bind:src="require(`../assets/pruebas/${imagen}`)"
      height="200px"
    ></v-img>


    <v-card-title>{{nombre}}
    </v-card-title>

    <v-card-subtitle>Fecha: {{fecha}}
    </v-card-subtitle>

    <v-card-actions>
      

    
      <v-dialog
        transition="dialog-bottom-transition"
        max-width="600"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            color="selectionPrueba"
            v-bind="attrs"
            v-on="on"
            text
          >Descargar</v-btn>
        </template>


        <template v-slot:default="dialog">
          <v-card>
            <v-toolbar
              color="selectionPrueba"
              
            >DESCARGAR IMAGEN</v-toolbar>
            <v-card-text>
              <div class="text-h2 pa-12">LO SENTIMOS, NO SE PUEDE DESCARGAR LA IMAGEN</div>
            </v-card-text>
            <v-card-actions class="justify-end">
              <v-btn
                text
                @click="dialog.value = false"
              >Close</v-btn>
            </v-card-actions>
          </v-card>
        </template>
      </v-dialog>

   

      <v-dialog
         transition="dialog-bottom-transition"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            color="popupPrueba"
            text
            v-bind="attrs"
            v-on="on"
          >Visualizar</v-btn>
        </template>
        <template v-slot:default="dialog">
          <v-card>
            <v-toolbar
              color="popupPrueba"
            >VISUALIZACIN IMAGEN</v-toolbar>
            
            <v-img
            v-bind:src="require(`../assets/pruebas/${imagen}`)"
          ></v-img>
            
            <v-card-actions class="justify-end">
              <v-btn
                text
                @click="dialog.value = false"
              >Close</v-btn>
            </v-card-actions>
          </v-card>
        </template>
      </v-dialog>
   



      <v-spacer></v-spacer>

      <v-btn
        icon
        @click="show = !show"
      >
        <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
      </v-btn>
    </v-card-actions>

    <v-expand-transition>
      <div v-show="show">
        <v-divider></v-divider>

        <v-card-text>
          Tipo:{{tipo}}
          <br><v-divider/>
          Informe:{{informe}}
        </v-card-text>
      </div>
    </v-expand-transition>
  </v-card>

</template>

<script>
export default {
  data: () => ({
    show: false,
  }),
  name: "CardHistorialCita",
  props: {
    nombre: String,
    imagen: String,
    fecha: String,
    informe: String,
    tipo: String,
  },
};
</script>
