<template>
  <v-container>
    <v-container fluid grid-list-md>
      <v-layout row wrap>
        <v-col cols="12" sm="4">
          <v-card color="cardAnalisis">
            <v-card-title primary class="title">Configuracin</v-card-title>
            <v-col cols="12" sm="9">
              <v-radio-group v-model="row" row>
                <v-radio label="Mostrar Anlisis" value="radio-1"></v-radio>
                <v-radio
                  label="Graficar Componentes en Sangre"
                  value="radio-2"
                ></v-radio>
              </v-radio-group>
              <v-menu
                v-if="row == 'radio-2'"
                ref="menu"
                v-model="menu"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                max-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="desde"
                    label="Desde"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  ref="picker"
                  v-model="desde"
                  :max="new Date().toISOString().substr(0, 10)"
                  min="1950-01-01"
                ></v-date-picker>
              </v-menu>
            </v-col>

            <v-col cols="12" sm="9">
              <v-menu
                v-if="row == 'radio-2'"
                ref="menu"
                v-model="menu2"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="hasta"
                    label="Hasta"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  ref="picker"
                  v-model="hasta"
                  :max="new Date().toISOString().substr(0, 10)"
                  min="1950-01-01"
                ></v-date-picker>
              </v-menu>
            </v-col>

            <v-container>
              <v-autocomplete
                v-if="row == 'radio-1'"
                :items="Analisis"
                label="Anlisis Realizado"
                prepend-icon="mdi-chart-bar"
                single-line
              ></v-autocomplete>

              <v-autocomplete
                v-if="row == 'radio-2'"
                :items="Componentes"
                chips
                label="Componentes en Sangre"
                multiple
                prepend-icon="mdi-chart-line"
                v-model="comps"
              >
              </v-autocomplete>
            </v-container>
          </v-card>
        </v-col>

        <v-col cols="12" sm="8">
          <v-card color="cardAnalisis">
            <v-card-title primary class="title"
              >Grficas de Componentes en Sangre</v-card-title
            >

            <barchart v-if="row == 'radio-1'" />

            <linechart :mycomp="compe" v-if="row == 'radio-2'" />
          </v-card>
        </v-col>
      </v-layout>
    </v-container>
  </v-container>
</template>

<script>
import BarChart from "../components/BarChart";
import LineChart from "../components/LineChart";

export default {
  components: {
    linechart: LineChart,
    barchart: BarChart,
  },
  data() {
    return {
      comps: null,
      desde: null,
      hasta: null,
      menu: null,
      menu2: null,
      row: "radio-1",

      Componentes: [
        { header: "Bioqumica" },
        { text: "cido rico" },
        { text: "Colesterol HDL" },
        { text: "Colesterol LDL" },
        { text: "Glucosa" },
        { text: "Proteinas" },
        { text: "Triglicridos" },
        { text: "Urea" },

        { divider: true },
        { header: "Hemograma" },
        { text: "Leucocitos" },
        { text: "Linfocitos" },
        { text: "Plaquetas" },

        { divider: true },
        { header: "Iones en Suero" },
        { text: "Calcio" },
        { text: "Cloro" },
        { text: "Fsforo" },
        { text: "Magnesio" },
        { text: "Potasio" },
        { text: "Sodio" },
      ],

      Analisis: [
        { header: "Ao 2018" },
        "02-1-2018",
        "02-3-2018",
        "10-12-2018",
        "02-02-2019",
        { divider: true },
        { header: "Ao 2019" },

        "03-06-2019",
        { divider: true },
        { header: "Ao 2020" },
        "06-09-2020",
        "08-10-2020",
      ],
    };
  },
  watch: {
    menu(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = "YEAR"));
    },
    menu2(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = "YEAR"));
    },
  },
};
</script>

<style></style>
